@import "./_globals";

.main {
    height:100%;
    a {
        color:$accent-color;
        text-decoration: none;
    }
    a:hover {
        text-decoration: underline;
        cursor: crosshair;
    }
}
.divider{
    display: block;
    background-color: black;
    height: 100%;
    width:5px;
}
.box {
    display: flex;
    flex-direction: row;
    align-items: flex-start;
    flex-wrap: wrap;
    align-content: space-between;
    width: 800px;
    margin-bottom: 10px;
    }
    .box .techs {
        flex-direction: column;
        align-self: flex-start;
    }
    .box .course {
        margin-left: auto;
        flex-direction: column;
        align-self: flex-end;
    }
   

.header {
    @include display-flex();
    flex-flow: row wrap;
    justify-content: space-between;
    font-weight: bold;
    font-size:20px;
    
    &__title {
        padding-bottom:10px;
    }  
}

.footer {
    margin-top:40px;
    margin-bottom:10px; 
}